///////////////////////////////////////////////////////////////////

.locatie {
    width: 100%;
    height: 1600px;
    // background-color: cadetblue;
    border: solid 2px rgb(232, 213, 213);
    // margin: 10px;
    padding: 10px;
}

.videoContainer {
    height: 100vh;
    border: solid 4px orange;
    overflow: hidden;
    display: flex;
    justify-content: center;
}


video {
    width: 100%;
    height: 100%;
    object-fit: contain;
    transform: scale(1.3);
}
@include small {
    video {
        transform: scale(1);
    }
}
@include smalll {
    video {
        transform: scale(1.3);
    }
}
