@mixin large {
    @media (min-width: 1200px) {
            @content;
    }
}

@mixin small {
    @media (max-width: 800px) {
            @content;
    }
}


.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100px;
    // background-color: rgb(171, 119, 148);
    border: solid 4px rgb(200, 181, 12);
}


